@import url('https://fonts.googleapis.com/css2?family=Righteous&family=Risque&display=swap');

body {
  margin: 0;
  padding: 0;
  background: black;
  font-family: Arial, sans-serif;
}

.container {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.image-container {
  width: 500px;
  height: 585px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image {
  width: 500px;
  height: 500px;
  position: relative;
  right: 3%;
}

.join-button {
  width: 480px;
  height: 89px;
  background: #F8D555;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  padding: 20px;
}

.join-text {
  color: white;
  font-size:48px;
  font-weight: bold;
  text-align: center;
  font-family: 'Risque', cursive;
}

.header {
  width: 95%;
  padding: 20px;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  color: white;
  font-size: 48px;
  font-family: 'Righteous', cursive;
}

.nav-dots {
  display: flex;
  gap: 16px;
}

.nav-icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .header-title {
    font-size: 36px;
  }

  .nav-icon {
    width: 20px;
    height: 20px;
  }

  .image-container {
    width: 90%;
    height: auto;
  }

  .image {
    width: 100%;
    height: auto;
  }

  .join-button {
    width: 80%;
  }

  .join-text {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .header-title {
    font-size: 24px;
  }

  .nav-dots {
    gap: 12px;
  }

  .nav-icon {
    width: 18px;
    height: 18px;
  }

  .join-button {
    width: 70%;
  }

  .join-text {
    font-size: 16px;
  }
}
